import React, { Component } from "react";
import Input from "./Input";
import Button from "../Common_Elements/Button";

export default class Form extends Component {
  constructor(props) {
    super(props);

    this.state = {
      name: "",
      email: "",
      title: "",
      message: ""
    };
  }

  handleChange = async e => {
    const { name, value } = e.target;
    await this.setState({ [name]: value });
  };

  stopSubmitting = e => {
    e.preventDefault();
  };

  render() {
    const { name, email, title, message } = this.state;
    return (
      <form onSubmit={this.stopSubmitting} className="contact__pane-form">
        <Input
          name="name"
          type="text"
          hint="Your Name..."
          value={name}
          handler={this.handleChange}
        />
        <Input
          name="email"
          type="text"
          hint="Your Email..."
          value={email}
          handler={this.handleChange}
        />
        <Input
          name="title"
          type="text"
          hint="Your Title..."
          value={title}
          handler={this.handleChange}
        />
        <textarea
          name="message"
          placeholder="Your Message..."
          value={message}
          onChange={this.handleChange}
          style={{
            flex: "1 1 100%",
            resize: "none",
            margin: "0 6px 3vh 0px",
            border: "none",
            padding: 5,
            minHeight: 150
          }}
        ></textarea>

        <Button message="Send Message" />
      </form>
    );
  }
}
