import React, { useState } from "react";
import ReactMapGL, { Marker } from "react-map-gl";

export default function Map() {
  const [viewport, setViewport] = useState({
    width: "100%",
    height: 450,
    latitude: 40.73061,
    longitude: -73.935242,
    zoom: 10
  });

  return (
    <ReactMapGL
      style={{ flex: "1 1 100%", margin: "3vh 0" }}
      {...viewport}
      onViewportChange={setViewport}
      mapboxApiAccessToken="pk.eyJ1IjoiZmFybGlnaHQiLCJhIjoiY2s2NTk5djBwMTR0cTN0cXdmbmI4ZDU0eiJ9.i8Q6S-dBA1rfN0SsAu-2bg"
    >
      <Marker
        latitude={40.7484}
        longitude={-73.9857}
        offsetLeft={-20}
        offsetTop={-10}
      >
        <div style={{ color: "#10c9c3", fontWeight: "bold" }}>The HQ</div>
      </Marker>
    </ReactMapGL>
  );
}
